// export default IndexPage
const IndexPage = () => {
    const intl = useIntl()

    const data = useStaticQuery(graphql`
        query MyQuery {
            allInternalPl {
                nodes {
                    home {
                        content {
                            alternative_id
                            type
                            title
                            desc
                        }
                    }
                }
            }
        }
    `)
    // console.log(data.allInternalPl.nodes[0].home.content)

    return (
        <Layout isHomepage>
            <Header page="1">
                <TextHeader
                    link
                    title={intl.formatMessage({ id: "home.header.title" })}
                    desc={intl.formatMessage({ id: "home.header.desc" })}
                />
            </Header>
            {data.allInternalPl.nodes[1].home.content.map((e, i) => {
                return (
                    <Wrapper
                        isBlack={i % 2 === 0}
                        videoSrc={`home-${i}`}
                        imgSrc={"dsadasd"}
                        title={intl.formatMessage({
                            id: `home.content.${i}.title`,
                        })}
                        desc={intl.formatMessage({
                            id: `home.content.${i}.desc`,
                        })}
                        z={i + 1}
                        isSecond={i === 0}
                        isLast={i === json.home.content.length - 1}
                    />
                )
            })}
        </Layout>
    )
}
export default IndexPage
